.navigation {
  position: relative;
  margin: 0 auto;
  text-align: center;

  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__link {
    display: block;
    padding: 24px 0;
    font-size: 20px;
    line-height: 20px;
    font-weight: 500;
    text-transform: uppercase;
    color: $black;
    border-top: 1px solid $dark-grey;
  }

  &__item:last-child &__link {
    border-bottom: 1px solid $dark-grey;
  }

  &__link:hover {
    opacity: 0.6;
  }

  &__link:focus {
    opacity: 0.3;
  }
}

.navigation--closed .navigation-toggle::before {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 24px;
  height: 2px;
  background-color: #222222;
  box-shadow: 0 8px 0 0 #222222, 0 16px 0 0 #222222;
}

.navigation--opened .navigation-toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    width: 19px;
    height: 2px;
    background-color: #000000;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }
}

@media (max-width: $mobile-width-only) {
  .navigation--closed .navigation__item {
    display: none;
  }

  .navigation--nojs .navigation-toggle {
    display: none;
  }
}

@media (min-width: $tablet-width) {
  .navigation {
    margin: 0;

    &__list {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-end;
      width: 515px;
    }

    &__link {
      padding: 18px 0 6px;
      margin-left: 35px;
      font-size: 18px;
      line-height: 25px;
      font-weight: 400;
      border-top: none;
    }

    &__item:last-child &__link {
      border-bottom: 2px solid transparent;
    }

    &__item:last-child &__link--active {
      border-bottom: 2px solid $green;
    }

    &__link--active {
      border-bottom: 2px solid $green;
    }
  }
}

@media (min-width: $desktop-width) {
  .navigation {
    &__list {
      width: 690px;
    }

    &__list--color .navigation__link {
      color: #ffffff;
    }

    &__list--color .navigation__link--active {
      border-color: #ffffff;
    }

    &__link {
      font-size: 20px;
    }
  }
}
